.nav-veilederpanel {
  background-color: #FFF;
  display: block;
  padding: 1rem 1rem 1rem 1rem;
  border-radius: 4px;
  font-family: 'Source Sans Pro', Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.375rem;
  font-weight: 400;
  position: relative;
  border-radius: 0.5rem;
  border: 2px solid #C6C2BF;
  min-height: 7.25rem;
  padding: 1.5rem;
  padding-left: 7rem;
  background: white;
  display: flex;
  align-items: center;
  text-align: left;
}
.nav-veilederpanel__content {
  width: 100%;
}
.nav-veilederpanel .nav-veileder {
  position: absolute;
  top: 1rem;
  left: 1rem;
}
.nav-veilederpanel--plakat {
  padding: 2rem;
  padding-top: 8.25rem;
}
.nav-veilederpanel--plakat .nav-veileder {
  left: 50%;
  margin-left: -3.125rem;
}
.nav-veilederpanel--kompakt:not(.nav-veilederpanel--plakat) {
  padding: 1.5rem;
  padding-left: 3.5rem;
  margin-left: 2.5rem;
}
.nav-veilederpanel--kompakt:not(.nav-veilederpanel--plakat) .nav-veileder {
  left: -40px;
}
.nav-veilederpanel--kompakt.nav-veilederpanel--plakat {
  margin-top: 3.125rem;
  padding: 2rem;
  padding-top: 4.25rem;
}
.nav-veilederpanel--kompakt.nav-veilederpanel--plakat .nav-veileder {
  top: -50px;
}
.nav-veilederpanel--suksess {
  border: 2px solid #9bd0b0;
}
.nav-veilederpanel--advarsel {
  border: 2px solid #ffd399;
}
.nav-veilederpanel--feilmelding {
  border: 2px solid #e3b0a8;
}
