.lenke {
  color: #0067C5;
  background: none;
  text-decoration: underline;
  cursor: pointer;
}
.lenke:hover,
.lenke--hover {
  text-decoration: none;
}
.lenke:focus,
.lenke--focus {
  outline: none;
  color: white;
  text-decoration: none;
  background-color: #254b6d;
  box-shadow: 0 0 0 2px #254b6d;
}
.lenke:active,
.lenke--active {
  color: white;
}
